import { useQuery } from "@tanstack/react-query";
import { fetchCoinHistory } from "./api";
import ApexChart from "react-apexcharts";

interface IChartProps {
  coinId: string;
}

interface IHistorical {
  time_open: string;
  time_close: string;
  open: number;
  high: number;
  low: number;
  close: number;
  volume: number;
  market_cap: number;
}

function Chart({ coinId }: IChartProps) {
  const { isLoading, data } = useQuery<IHistorical[]>({
    queryKey: ["ohlcv", coinId],
    queryFn: () => fetchCoinHistory(coinId),
    // refetchInterval: 10000,
  });
  return (
    <div>
      {isLoading ? (
        "Loading chart..."
      ) : (
        <ApexChart
          type="line"
          series={[
            {
              name: "Price",
              data: data?.map((price) => price.close) ?? [],
            },
          ]}
          options={{
            //   
            chart: {
              height: 300,
              width: 500, // toolbar 
              toolbar: { show: false },
              // fontFamily: "galmuri9" //font 
              background: "transparent",
              type: "area",
            },
            //   
            theme: {
              mode: "dark",
              // palette: "palette1",
              // monochrome: {
              //   enabled: false,
              //   color: "#c7ecee",
              //   shadeTo: "dark",
              //   shadeIntensity: 0.65,
              // },
            },
            //   
            stroke: {
              curve: "smooth", //  Peak  
              width: 4, //   
            },
            // grid 
            grid: {
              show: false
            },
            // x 
            xaxis: {
              labels: {
                show: false
              },
              axisTicks: {
                show: false
              },
              axisBorder: {
                show: false
              },
              categories: data?.map((price) => new Date(Number(price.time_close) * 1000).toUTCString()) ?? [],
              type: "datetime",
            },
            // y 
            yaxis: {
              show: false
            },
            fill: {
              type: "gradient",
              gradient: {
                gradientToColors: ["#0be881"],
                stops: [0, 100],
              }
            },
            colors: ["#0fbcf9"],
            tooltip: {
              y: {
                formatter: (value) => `${value.toFixed(2)}`
              }
            }
          }}
        ></ApexChart>
      )}
    </div>
  );
}

export default Chart;
